  position: absolute;
  top: -2px;
  left: -2px;
}

.button-zero:after, .button-six:after, .button-twelve:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(90deg, #fff, #fff);
  border-radius: 4px;
  transition: all .4s;
  position: absolute;
  top: 0;
  left: 0;
}

.button-zero:hover, .button-six:hover, .button-twelve:after {
  color: #fff;
  transition: all .3s;
}

.button-zero:hover:after, .button-six:hover:after, .button-twelve:hover:after {
  background: linear-gradient(90deg, #fd7f34, #bd155b);
}

.button-zero:active:after, .button-six:active:after, .button-twelve:active:after {
  background: linear-gradient(90deg, #d96d2d, #760f3a);
}

.button-zero:focus-visible, .button-six:focus-visible, .button-twelve:focus-visible {
  box-shadow: 0 0 0 3px #fd7f34;
}

